@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 15px; }

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px orange;
  border-radius: 10px; }

/* Handle */
::-webkit-scrollbar-thumb {
  background: orange;
  border-radius: 10px; }

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: orangered; }

html {
  margin: 0;
  padding: 0; }

body {
  background: #fc00ff;
  /* fallback for old browsers */
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #00dbde, #fc00ff);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ }

.index {
  display: flex;
  flex-direction: column; }
  .index .index-content {
    margin-top: 80px;
    display: grid;
    grid-template-columns: 2fr 1fr; }
  .index .profile {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 50px;
    height: auto;
    border-bottom: 1px solid darkgrey;
    padding-bottom: 20px; }
    .index .profile .profile-image img {
      height: 100px;
      width: 100px;
      border-radius: 50%; }
    .index .profile .profile-info i {
      color: white;
      margin-left: 10px; }
    .index .profile .profile-info img {
      height: 50px;
      width: 50px; }
    .index .profile .profile-info p {
      margin-top: 15px;
      font-family: 'Roboto', sans-serif;
      color: orange;
      font-size: 25px;
      font-weight: bold;
      line-height: 15px; }
    .index .profile .profile-slo p {
      color: wheat;
      font-family: 'Roboto Slab', serif;
      margin: 0;
      padding: 0; }

.content {
  display: flex;
  flex-direction: column;
  margin-top: 100px; }
  .content .icons {
    flex: 1 1;
    display: flex;
    justify-content: space-around; }
    .content .icons .not-active {
      text-decoration: none;
      color: black;
      font-size: 48px;
      transition: all 0.1s ease-out; }
      .content .icons .not-active:hover {
        cursor: pointer;
        font-size: 50px; }
  .content .about-me {
    flex: 1 1;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 20px; }
    .content .about-me p {
      color: white;
      font-size: 20px; }

.skills {
  position: relative; }
  .skills h3 i {
    margin-right: 50px;
    color: orange; }
  .skills .skill-progress {
    display: grid;
    grid-gap: 10px 2em;
    grid-template-columns: 50px 1fr 50px; }
    .skills .skill-progress i {
      font-size: 30px; }
    .skills .skill-progress p {
      color: orange;
      font-weight: bold; }
    .skills .skill-progress img {
      height: 35px;
      width: 35px; }
  .skills .follow-me {
    position: absolute;
    top: 80%;
    right: 35%;
    text-align: center; }
    .skills .follow-me i {
      font-size: 40px; }
      .skills .follow-me i:hover {
        cursor: pointer; }

/* 
  ##Device = Laptops, Desktops
  ##Screen = B/w 1025px to 1280px
*/
/* 
  ##Device = Tablets, Ipads (portrait)
  ##Screen = B/w 768px to 1024px
*/
@media (min-width: 768px) and (max-width: 1024px) {
  .profile-info p {
    color: white; } }

/* 
  ##Device = Tablets, Ipads (landscape)
  ##Screen = B/w 768px to 1024px
*/
@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {
  .profile-info p {
    color: white; } }

/* 
  ##Device = Low Resolution Tablets, Mobiles (Landscape)
  ##Screen = B/w 481px to 767px
*/
/* 
  ##Device = Most of the Smartphones Mobiles (Portrait)
  ##Screen = B/w 320px to 479px
*/
@media (min-width: 320px) and (max-width: 480px) {
  .profile {
    flex-direction: column;
    align-items: center; }
  .profile-info p {
    color: white; } }

